#searchBox {
  boxSizing: border-box;
  border: 1px solid;
  border-color: rgba(0, 0, 0, 0.2);
  width: 90%;
  float:right;
  height: 32px;
  padding: 0 12px;
  borderRadius: 3px;
  boxShadow: 0 2px 6px rgba(0, 0, 0, 0.3);
  fontSize: 14px;
  outline: none;
  textOverflow: ellipses;
  color: black;
}

#formSearchBox {
  boxSizing: border-box;
  border: 1px solid;
  border-color: rgba(0, 0, 0, 0.2);
  width: 100%;
  height: 32px;
  padding: 0 12px;
  borderRadius: 3px;
  boxShadow: 0 2px 6px rgba(0, 0, 0, 0.3);
  fontSize: 14px;
  outline: none;
  textOverflow: ellipses;
  color: black;
  
}



