.checked {
    background-color: #4CAF50;
}

.unchecked {
  background-color: white;
}

.flasher {
  width: auto;
  text-decoration: underline;
  color: white;

  -webkit-animation-name: flash;

          animation-name: flash;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
}

.flasher a:hover {
  text-decoration: underline;
}

.avBox {
  border: solid 2px black;
  width: 50em;
  text-decoration: underline;
  font-size: 1.5vmin;
}

.toggleDay {
  width: 50em;
  text-decoration: underline;
  font-size: 1.5vmin;
}

.selectedtext {
  padding: 2px;
}

.unselectedtext {
  color: black;
  padding: 2px;
}

@-webkit-keyframes flash {
  /* from {border-bottom: 1px solid white; border-top: 1px solid white; }
  to {border-bottom: 1px solid black; border-top: 1px solid black;} */
  from{-webkit-text-decoration-color: white;text-decoration-color: white;}
  to{-webkit-text-decoration-color: transparent;text-decoration-color: transparent;}
}

@keyframes flash {
  /* from {border-bottom: 1px solid white; border-top: 1px solid white; }
  to {border-bottom: 1px solid black; border-top: 1px solid black;} */
  from{-webkit-text-decoration-color: white;text-decoration-color: white;}
  to{-webkit-text-decoration-color: transparent;text-decoration-color: transparent;}
}



