.received_team .message_text {
  background: #e0ebeb;
  color: black;
  border: 1px solid #e0ebeb;
}

.received_fixture_team .message_text {
  background: #e0ebeb;
  color: black;
  border: 2px solid #4CAF50;
}

.received_fixture_opp .message_text {
  background: #e0ebeb;
  color: black;
  border: 2px solid #ff4d4d;
}

.sent .message_text {
  background: #4CAF50;
  border: 1px solid #4CAF50;
  color: white;
}

.message_text {
  border-radius: 5px;
  padding: 5px;
  display: inline-block;
  font-size: 1.8vh;
}

.received_team .sender, .received_fixture_team .sender,
  .received_fixture_opp .sender {
    text-align: left;
    grid-area: sd;
}

.sent .sender {
  text-align: right;
  grid-area: sd;
}

.sender {
  color: white;
  padding-top: 5px
}

.sent .message {
  grid-area: ms;
  text-align: right;
  float: right;
}

.received_team .message, .received_fixture_team .message,
  .received_fixture_opp .message  {
    grid-area: ms;
    float: right;
    text-align: left;
}

.message {
  font-size: 2vh;
}

.sent {
    display: grid;
    grid-template-columns: repeat(9, 1fr);
    grid-auto-rows: minmax(0px, auto);
    grid-template-areas:
      ". .  .  .  .  .  sd sd sd"
      ". .  ms ms ms ms ms ms ms"
}

.received_team, .received_fixture_team, .received_fixture_opp {
    display: grid;
    grid-template-columns: repeat(9, 1fr);
    grid-auto-rows: minmax(0px, auto);
    grid-template-areas:
      "sd sd sd .  .  .  .  . . "
      "ms ms ms ms ms ms ms . . "
}

.sent, .received_team, .received_fixture_team, .received_fixture_opp {
  margin-right: 10px;
  margin-left: 10px;
  margin-bottom: 2px;
  text-decoration: none;
  color: black;
}



